import https from "@/utils/https";

// 
export function getAll(params) {
    return https({
        url: '/collection/getList',
        method: 'get',
        params: params
    });
}

// 
export function updateAll(data) {
    return https({
        url: '/setList',
        method: 'post',
        transformRequest: [function(data) {
            let ret = '';
            for(let it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret;
        }],
        data: data,
    });
}

